.uppercase {
  text-transform: uppercase;
}
.h5-regular {
  font-weight: normal;
}
@font-face {
  font-family: 'Icons';
  src: url("../fonts/icons.eot");
  src: url("../fonts/icons.eot?#iefix") format('embedded-opentype'), url("../fonts/icons.woff2") format('woff'), url("../fonts/icons.woff") format('woff'), url("../fonts/icons.ttf") format('truetype'), url("../fonts/icons.svg#icons") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
@font-face {
  font-family: 'Suisse Intl';
  src: url("../fonts/SuisseIntl-Medium-WebXL.eot");
  src: url("../fonts/SuisseIntl-Medium-WebXL.woff2") format('woff'), url("../fonts/SuisseIntl-Medium-WebXL.woff") format('woff'), url("../fonts/SuisseIntl-Medium-WebXL.ttf") format('truetype'), url("../fonts/SuisseIntl-Medium-WebXL.svg") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
@font-face {
  font-family: 'Suisse Neue';
  src: url("../fonts/SuisseNeue-Regular-WebXL.eot");
  src: url("../fonts/SuisseNeue-Regular-WebXL.woff2") format('woff'), url("../fonts/SuisseNeue-Regular-WebXL.woff") format('woff'), url("../fonts/SuisseNeue-Regular-WebXL.ttf") format('truetype'), url("../fonts/SuisseNeue-Regular-WebXL.svg") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: 'Suisse Intl', 'sans-serif';
}
body,
p {
  font-family: 'Suisse Neue';
  font-size: 16px;
  line-height: 24px;
}
h4 {
  font-size: 16px;
}
.gray-shadow {
  background: #eee;
  -webkit-box-shadow: 0px 0px 15px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 0px 0px 15px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 0px 0px 15px 0px rgba(50, 50, 50, 0.75);
}
main.main-wrapper {
  padding: 0 10px 10px 10px;
  margin-top: 0;
}
html.ios {
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
html.ios,
html.ios body {
  height: initial !important;
}
#image-menu-container {
  position: relative;
}
#image-menu-container #image-menu-text {
  position: relative;
}
#image-menu-container #image-menu-text a {
  color: white;
}
#image-menu-container #menu-image {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 46%;
}
#main-menu-front-page {
  margin: 0;
}
#main-menu-front-page #menu-image {
  position: absolute;
  left: 46%;
}
#main-menu-front-page > .row {
  padding-top: 0;
  padding-bottom: 0;
}
#image-menu-container.normal-menu #image-menu-text a {
  color: black;
}
#image-menu-container.normal-menu .ui.vertical.segment {
  padding-bottom: 0;
}
#top-menu.ui.fluid.menu {
  position: relative;
  margin: 0 0;
  background: transparent;
  border: 0;
  -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  border-radius: 0;
}
#top-menu.ui.fluid.menu .item:before {
  width: 0;
}
#top-menu.ui.fluid.menu #menu-image {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 46%;
}
.ui.fluid.top.fixed.menu #menu-image {
  position: absolute;
  left: 46%;
}
form ::-webkit-input-placeholder {
  color: white !important;
  font-family: 'Suisse Intl', 'sans-serif';
  font-weight: normal;
  text-transform: uppercase;
}
form :-moz-placeholder {
  /* Firefox 18- */
  color: white !important;
  font-family: 'Suisse Intl', 'sans-serif';
  font-weight: normal;
  text-transform: uppercase;
}
form ::-moz-placeholder {
  /* Firefox 19+ */
  color: white !important;
  font-family: 'Suisse Intl', 'sans-serif';
  font-weight: normal;
  text-transform: uppercase;
}
form :-ms-input-placeholder {
  color: white !important;
  font-family: 'Suisse Intl', 'sans-serif';
  font-weight: normal;
  text-transform: uppercase;
}
form input[type="text"] {
  font-family: 'Suisse Intl', 'sans-serif' !important;
  font-weight: normal;
}
footer .sidebar-footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
footer .sidebar-footer a {
  color: white;
  font-size: 14px;
}
footer .sidebar-footer a:hover {
  text-decoration: underline;
}
footer .column a.facebook-icon {
  color: white;
}
footer .column a:hover.facebook-icon {
  color: lightblue;
}
footer h5 {
  font-weight: normal;
  line-height: 20px;
}
.slides-container .container {
  position: absolute;
  top: 90%;
  color: white;
  margin-left: 30px;
}
.slides-container .container h4 {
  letter-spacing: 0.09em;
  font-weight: bold;
}
.slides-link {
  margin: 0 auto;
  position: absolute;
  z-index: 3;
  top: 46%;
  width: 100%;
}
.slides-link a {
  position: absolute;
  display: block;
  z-index: 99999;
}
.landing-content {
  background: #fefefe;
  padding-bottom: 0;
  margin-bottom: -25px;
}
a.landing-info {
  color: black;
  text-decoration: underline;
}
a:hover.landing-info {
  background: #dbd1c7;
}
.film-content p {
  font-size: 1.11em;
  line-height: 1.4em;
}
#the-movie a.movie-website {
  color: black;
}
#the-movie a:hover.movie-website {
  text-decoration: underline;
}
.program {
  margin-bottom: 10px;
}
#week-program.program .ui.grid .column .img-container {
  position: relative;
}
#week-program.program .ui.grid .column .img-container a:hover .film-image {
  filter: brightness(80%);
}
#week-program.program .ui.grid .column .img-container .film-image {
  position: relative;
  left: 0;
  top: 0;
}
#week-program.program .ui.grid .column .img-container .film-title {
  color: red;
  z-index: 100;
  position: absolute;
  color: white;
  font-size: 20px;
  font-weight: bold;
  left: 10px;
  top: -15px;
  text-shadow: 0.2px 0.2px 0px #969696;
  letter-spacing: 1px;
}
#week-program.program .ui.grid .column h3.film-week-detail {
  margin-left: 10px;
  text-transform: uppercase;
  margin-top: 5px;
}
#week-program.program .ui.grid .column h5 {
  margin-top: 0;
  margin-left: 10px;
}
#month-program.program .ui.grid .column h5 {
  margin: 0;
}
#sidebar-computer-menu .title,
#sidebar-mobile-menu .title {
  color: #fefefe;
}
#sidebar-computer-menu .content,
#sidebar-mobile-menu .content {
  margin-left: 50px;
}
#sidebar-computer-menu .menu-sidebar-container ul,
#sidebar-mobile-menu .menu-sidebar-container ul {
  list-style-type: none;
  padding: 0;
}
#sidebar-computer-menu .menu-sidebar-container ul li a,
#sidebar-mobile-menu .menu-sidebar-container ul li a {
  color: white;
}
#sidebar-computer-menu .menu-sidebar-container ul li a:hover,
#sidebar-mobile-menu .menu-sidebar-container ul li a:hover {
  text-decoration: underline;
}
#sidebar-computer-menu .content h5.sidebar-h5,
#sidebar-mobile-menu .content h5.sidebar-h5 {
  font-weight: normal;
}
#sidebar-computer-menu .content .ui.grid .column h5.sidebar-h5,
#sidebar-mobile-menu .content .ui.grid .column h5.sidebar-h5 {
  font-weight: normal;
}
#sidebar-computer-menu table,
#sidebar-mobile-menu table {
  border-spacing: 10px;
}
#sidebar-computer-menu table td,
#sidebar-mobile-menu table td {
  padding: 5px 10px;
  vertical-align: top;
}
body {
  margin: 12px !important;
}
